.TodoItem {
  padding:.5em;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
}
.TodoItem> input {
}
.TodoItem> .title {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding-left: .5em;
}
.TodoItem> .del-btn{
  color: #888;
  display:block;
  cursor:pointer;
  font-size:1.2em;
}
.TodoItem> .del-btn i {
  font-style: normal;
  vertical-align:text-bottom;
}
.TodoItem> .del-btn:hover {
  color:#ff4f4a;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}


